import React, { useState } from "react";
import { useSelector } from "react-redux";
import { useHistory } from "react-router";
import { toast } from "react-toastify";
import api from "../../../utils/apiUtils";
import ROUTES from "../../../routes/index";
import Loader from "../../../component/Loader";
import "./Register.css";

function RegisterPage() {
  const loading = useSelector((state) => state.registerUserReducer.loading);
  const history = useHistory();
  const [state, setState] = useState({
    taiKhoan: "",
    matKhau: "",
    hoTen: "",
    birthdaytime: "",
    email: "",
    soDt: "",
    maNhom: "GP14",
    maLoaiNguoiDung: "KhachHang",
  });

  const [formErrors, setFormErrors] = useState({
    taiKhoan: "",
    matKhau: "",
    birthdayTime: "",
    hoTen: "",
    email: "",
    soDt: "",
  });

  const handleOnChange = (event) => {
    const { name, value } = event.target;
    setState({
      ...state,
      [name]: value,
    });
  };

  const handleErrors = (event) => {
    const { name, value } = event.target;

    let mess = value.trim() === "" ? " Khng b trng!" : "";

    switch (name) {
      case "taiKhoan":
        if (value && value.length <= 4) {
          mess = "  di k t t 5 tr ln";
        }
        break;

      case "matKhau":
        if (value && value.length <= 4) {
          mess = "  di k t t 5 tr ln";
        }
        break;

      case "hoTen":
        if (value && !value.match(/^[a-zA-Z ]{2,30}$/)) {
          mess = "H v tn khng hp l";
        }
        break;

      case "email":
        if (value && !value.match(/[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$/)) {
          mess = "Email khng hp l";
        }
        break;

      case "soDt":
        if (value && value.length < 10) {
          mess = " ST phi t nht 10 s";
        }
        break;

      default:
        break;
    }

    setFormErrors({ ...formErrors, [name]: mess });
  };
  const handleRegisterUser = (event) => {
    event.preventDefault();

    api
      .post("QuanLyNguoiDung/DangKy", state)
      .then((result) => {
        toast("register success");
        history.push(`${ROUTES.home}${ROUTES.login}`);
      })
      .catch((error) => {
        // dispatch(actRegisterUserFailed(error));
      });
  };

  if (loading) return <Loader />;
  return (
    <div className="registerPage">
      <form className="container" onSubmit={handleRegisterUser}>
        <h3 className="registerPage--title">NG K</h3>
        <div className="form-group">
          <span>H v tn</span>
          <input
            className="form-control"
            name="hoTen"
            placeholder="Nhp vo h v tn"
            onChange={handleOnChange}
            onBlur={handleErrors}
          />
          <span className="registerPage--error">{formErrors.hoTen}</span>
        </div>
        <div className="form-group">
          <span>Ngy sinh</span>
          <div className="input-group">
            <input
              type="datetime-local"
              id="birthdaytime"
              name="birthdayTime"
              onChange={handleOnChange}
              onBlur={handleErrors}
            />
            <span className="registerPage--error">
              {formErrors.birthdayTime}
            </span>
          </div>
        </div>

        <div className="form-group">
          <span>Email</span>
          <input
            className="form-control"
            name="email"
            placeholder="Nhp vo Email"
            onChange={handleOnChange}
            onBlur={handleErrors}
          />
          <span className="registerPage--error">{formErrors.email}</span>
        </div>
        <div className="form-group">
          <span>Ti khon</span>
          <input
            className="form-control"
            name="taiKhoan"
            placeholder="Nhp vo ti khon"
            onChange={handleOnChange}
            onBlur={handleErrors}
          />
          <span className="registerPage--error">{formErrors.taiKhoan}</span>
        </div>
        <div className="form-group">
          <span>Mt khu</span>
          <input
            className="form-control"
            name="matKhau"
            placeholder="Nhp vo mt khu"
            onChange={handleOnChange}
            onBlur={handleErrors}
          />
          <span className="registerPage--error">{formErrors.matKhau}</span>
        </div>
        <div className="form-group">
          <span>ST</span>
          <input
            className="form-control"
            name="soDt"
            placeholder="Nhp vo ST"
            onChange={handleOnChange}
            onBlur={handleErrors}
          />
          <span className="registerPage--error">{formErrors.soDt}</span>
        </div>
        <div className="form-group registerPage--btn">
          <button type="submit" className="registerPage--button">
            NG K
          </button>
        </div>
      </form>
    </div>
  );
}

export default RegisterPage;
